<template>
    <div id="rentDetailShare">
        <assistant :cityId="cityId" :agent="apiData.simpleAgentModel" :callBigDataParams="getUvParamsString({ eventName : 2057002 })" />
       <download-app />
       <!---->
       <swiper :options="pageConfs.swiperOption">
            <swiper-slide v-for="(slide , index) in apiData.simpleHouseRentDetailInfo.houseImageAndVideoList" :key="slide.imgKey">
                <video :src="slide.videoSrc" :poster="slide.imageSrc" controls="controls" preload="none"  class="img-responsive" style="width:100%;height : 210px ; " v-if="slide.video"></video>                
                <img :src="slide.imageSrc" class="img-responsive" v-else>
                <div class="pagination">{{ pageStates.swiperActiveIndex }} / {{ apiData.simpleHouseRentDetailInfo.houseImageAndVideoList.length }}</div>
            </swiper-slide>
        </swiper> 
       <!---->
       <div class="wk-panel summary">
            <div class="panel-header">
                <div class="message">
                    <h1>{{ apiData.simpleHouseRentDetailInfo.subEstateName }}</h1>
                    <div class="money">{{ apiData.simpleHouseRentDetailInfo.rentPrice }}  / </div>
                </div>
                <!--<ul>
                    <li><span></span><span>20</span></li>
                    <li><span></span><span>140</span></li>
                    <li><span></span><span>88</span></li>
                </ul>-->
            </div>
            <!--<div class="update-time">
                <ul>
                    <li><dl><dt></dt><dd>{{apiData.simpleHouseRentDetailInfo.houseId}}</dd></dl></li>
                    <li><dl><dt></dt><dd>{{apiData.simpleHouseRentDetailInfo.publishHouseTime.slice(5)}}</dd></dl></li>
                    <li><dl><dt></dt><dd>{{apiData.simpleHouseRentDetailInfo.updateHouseTime.slice(5)}}</dd></dl></li>
                </ul>
            </div>-->
        </div>
        <!---->
        <div class="wk-panel house-info">
            <div  class=" panel-body lr-padding tb-padding">
                <ul class="column-3">
                    <li v-if="apiData.simpleHouseRentDetailInfo.houseId<10000"><dl><dt>{{ apiData.simpleHouseRentDetailInfo.payTypeStr }}</dt><dd></dd></dl></li>
                    <li><dl><dt>{{ apiData.simpleHouseRentDetailInfo.houseTypeStr }}</dt><dd></dd></dl></li>
                    <li><dl><dt>{{ apiData.simpleHouseRentDetailInfo.spaceArea }} m&sup2;</dt><dd> </dd></dl></li>
                </ul>
                <hr>
                <ul class="outline">
                    <li class="percent-70"><dl><dt></dt><dd>{{ apiData.simpleHouseRentDetailInfo.houseChildTypeStr }}</dd></dl></li>
                    <li class="percent-30"><dl><dt></dt><dd>{{ apiData.simpleHouseRentDetailInfo.renovationStr }}</dd></dl></li>
                    <li class="percent-70"><dl><dt></dt><dd>{{ apiData.simpleHouseRentDetailInfo.houseFloor}}</dd></dl></li>
                    <li class="percent-30"><dl><dt></dt><dd>{{ apiData.simpleHouseRentDetailInfo.orientationStr }}</dd></dl></li>
                    <li class="percent-100"><dl><dt></dt><dd>{{ apiData.simpleHouseRentDetailInfo.subEstateInitName }}</dd></dl></li>
                </ul>
                <hr>
                <ul class="subway-school">
                    <li><dl><dt></dt><dd>{{ apiData.simpleHouseRentDetailInfo.subwayStr }}</dd></dl></li>
                    <li><dl><dt></dt><dd>{{ apiData.simpleHouseRentDetailInfo.schoolStr }}</dd></dl></li>
                </ul>
            </div>
        </div>
        <!---->
        <div class="wk-panel support top-gap">
            <div class="panel-header"></div>
            <ul  class="panel-body  lr-padding tb-padding">
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasBad }" class="iconfont icon-bed "></li>
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasTV }" class="iconfont icon-tv "></li>
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasInternet }" class="iconfont icon-internet "></li>
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasAirConditioning }" class="iconfont icon-airconditioner right"></li>
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasWashMachine }" class="iconfont icon-washer"></li>
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasWardrobe }" class="iconfont icon-wardrobe "></li>
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasWaterHeater }" class="iconfont icon-heater "></li>
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasHeating }" class="iconfont icon-heating right"></li>
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasBalcony }" class="iconfont icon-balcony"></li>
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasGas }" class="iconfont icon-gas "></li>
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasMicrowave }" class="iconfont icon-microwave "></li>
                <li :class=" { no : ! apiData.simpleHouseRentDetailInfo.houseSupporting.hasFridge }" class="iconfont icon-refrigerator right"></li>       
            </ul>
        </div>
        <!---->
        <div class="wk-panel description top-gap">
            <!---->
            <div v-if="apiData.simpleHouseRentDetailInfo.houseId<1000000" class="self-house">
                <div class="panel-header"></div>
                <div  class="panel-body  lr-padding tb-padding">
                    <dl><dt></dt><dd>{{ apiData.simpleHouseRentDetailInfo.sellPoint || "" }}</dd></dl>
                    <dl><dt></dt><dd>{{ apiData.simpleHouseRentDetailInfo.ownerMotivation || "" }}</dd></dl>
                    <dl><dt></dt><dd>{{ apiData.simpleHouseRentDetailInfo.aroundSupport || "" }}</dd></dl>
                </div>
            </div>
            <!---->
            <div v-else-if="apiData.simpleHouseRentDetailInfo.houseId>1000000&&text.length>30" class="outside-house ">
                <div class="panel-header"></div>
                <div class="outside-info panel-body lr-padding" :class="{moreInfo:moreInfo}" ref="sansInfo">{{text}}</div>
                <div  v-if="moreInfo" @click="outsideMoreInfo" class="is-look lr-padding">{{isLook}}</div>
            </div>
        </div>
        <!---->
        <div class="wk-panel top-gap estate-info">
            <div class="info panel-header"></div>
            <div class="estate-detail">
                <div class="estate-img">
                    <img :src="estate.src" alt="">
                </div>
                <div class="estate-text">
                    <ul>
                        <li class="estate-name">{{estate.name}}</li>
                        <li class="jiantou">
                            <span>{{estate.year}}</span><span class="division">|</span><span>{{estate.total}}</span>
                            <router-link :to="'/estate/detail/share'" class="iconfont icon-arrowR skip"></router-link>
                        </li>
                        <li>{{estate.address}}</li>
                    </ul>
                </div>
            </div>
        </div>
        <!---->
        <div class="wk-panel location top-gap">
            <div class="panel-header"></div>
            <div class="location-container">
                <img :src="'https://api.map.baidu.com/staticimage/v2?ak=GByG2pAz1WlmY7wX1rlIM4nd&width=640&height=426&center=' + apiData.simpleHouseRentDetailInfo.longitude + ',' + apiData.simpleHouseRentDetailInfo.latitude + '&zoom=18'" class="img-responsive">
                <div class="cover">
                    <i class="iconfont icon-arrowTS"></i><span>{{ apiData.simpleHouseRentDetailInfo.subEstateInitName }}</span>
                </div>
                <div class="mark"></div>
            </div>
        </div>
        <!---->
        <div class="alike-house top-gap wk-panel" v-if="rent.data.rentList.length">
            <div class="house-recommend panel-header"></div>
            <rent-sources :statusStyle="styleStatus" :dataItems="rent.data.rentList" :agentId="agentId"></rent-sources>
        </div>
        <!---->
    </div>
</template>

<script>
    import downloadApp from "@/components/common/downloadApp" ;
    import assistant from "@/components/common/assistant" ;
    import rentSources from "@/components/common/rentSources";
    import rent from "../../../../mock/rent/detail.json";
    import $ from "jquery";
    import { swiper , swiperSlide } from "vue-awesome-swiper" ;
    import apiDataFilter from "@/libraries/apiDataFilter" ;
    export default {
      name : "rentDetailShare" ,
      data () {
          return {
              pageStates : {
                  swiperActiveIndex : 1 //
              } ,
              pageConfs : {
                  swiperOption : {  //  swiper
                      name : "currentSwiper" ,
                      // Swiper
                      autoplay : 0 , //msslide
                      grabCursor : true ,  //trueSwiper
                      setWrapperSize : true ,
                      autoHeight : false ,  //truewrappercontainerslide
                      //
                      onInit : swiper => {
                          this.pageStates.swiperActiveIndex = swiper.activeIndex + 1 ;
                      } ,
                      onSlideChangeEnd : swiper => {
                        this.pageStates.swiperActiveIndex = swiper.activeIndex + 1 ;
                      }
                  }
              } ,
              apiData : {
                  simpleHouseRentDetailInfo : {} ,
                  simpleAgentModel : {}
              },
              text:`IWO
              IWO
              
              IWO
              IWO
              IWO
              
              IWO
              if`,
              moreInfo:true,//5
              textHeight:'',//
              isLook:'',//
              estate:{
                  //;
                  src:'https://img.wkzf.com/e15d7d3d78974f1dbe076c7e8967c714.CL',
                  name:'',
                  year:'1993',
                  total:'150',
                  address:'996'
              },
              styleStatus:false,//
              agentId:'',
              rent:rent,
              outsideHouseAccount:true//;
          }
      } ,
      methods : {
          //
          getUvParamsString : function({ eventName , otherParams }) {
              let eventParam = { house_id : this.apiData.simpleHouseRentDetailInfo.houseId } ;
              if(otherParams !== undefined && otherParams !== null ) {
                  eventParam = Object.assign( eventParam , otherParams ) ;
              }
              return encodeURIComponent(JSON.stringify({
                  eventName : eventName ,
                  eventParam : eventParam ,
                  type : 2
              })) ;
          },
         //
         outsideMoreInfo(){
            if($('.is-look').text()==''){
                this.$refs.sansInfo.style.height=this.textHeight+'px';
                this.isLook=''
            };
            if($('.is-look').text()==''){
                this.$refs.sansInfo.style.height=25*5+'px';
                this.isLook=''
            }
         }
      } ,
      created() {
          let houseId = this.$route.params.houseId ;
          let agentId = this.$route.params.agentId ;
          this.cityId = this.$route.query.cityId;
          this.agentId=agentId;
		  let self = this;
          apiDataFilter.request({
              apiPath : "rent.detail" ,
              data : { "houseId" : houseId , "agentId" : agentId } ,
              successCallback : res => {
                  console.log(res)
                  Object.assign(this.$data.apiData , res.body.data) ;
                  //document.title = "" ;
                  self.$nativeBridge.invokeMethod('updateTitle',[''],function(){
                    console.log('');
                  },function(){
                    console.log('');
                  });
                  this.$nextTick(function(){
                     if(this.apiData.simpleHouseRentDetailInfo.houseId>1000000){
                        let houseInfo=this.$refs.sansInfo.clientHeight;
                        this.textHeight=this.$refs.sansInfo.clientHeight;
                        //   ;
                        if(houseInfo/25>5){
                            this.$refs.sansInfo.style.height=25*5+'px';
                        }
                     }
                  });
                  //
                  let wechatShare = res.body.data.weChatShare ;
                  this.$wechatShare({
                      "title" : wechatShare.title ,
                      "timelineTitle" : wechatShare.timelineTitle ,
                      "content" : wechatShare.content ,
                      "imgUrl" : wechatShare.picUrl ,
                      "success" : function() { console.log("") ;  } ,
                      "fail" : function() { console.log("") ;  } ,
                      "cancel" : function() { console.log("") ; } ,
                      "complete" : function() { console.log("") ; }
                  }) ;

                  //
                this.$bigData({
                  pageName : 2057 ,
                  pageParam : {
                      agent_id : agentId ,
                      house_id : this.apiData.simpleHouseRentDetailInfo.houseId
                  } ,
                  type : 1
                }) ;
              }
          }) ;
      } ,
      components : {
          downloadApp ,
          assistant ,
          rentSources,
          swiper ,
          swiperSlide
      }
    }
</script>

<style lang="less" scoped>
    @import "../../../../src/less/rent/detail.less" ;
</style>



